"use client";
import { useEffect, useState } from "react";
import getCurrentWeather from "@/utils/currentweather";
import { Plus, Search } from "lucide-react";

const Home = () => {
  const [currentWeather, setCurrentWeather] = useState(null);
  const [query, setQuery] = useState("");
  const [savedCities, setSavedcities] = useState([]);
  useEffect(() => {
    console.log(savedCities);
  }, [savedCities]);

  useEffect(() => {
    getCurrentWeather("Delhi, India")
      .then(setCurrentWeather)
      .catch(console.error);
  }, []);
  console.log(currentWeather);

  function handleSearch(e) {
    e.preventDefault();
    const city = query.trim();
    if (!city) return;
    getCurrentWeather(city).then(setCurrentWeather).catch(console.error);
  }

  function handlebookmark() {
    const cityname = currentWeather?.location?.name;
    if (!cityname) return;
    if (savedCities.includes(cityname)) return;
    setSavedcities((prev) => [...prev, cityname]);
  }

  return (
    <>
      <div className="flex flex-col items-center justify-center h-screen space-y-4 text-white bg-gradient-to-b from-cyan-600 to-blue-700">
        <div className="position-relative">
          <form>
            <input
              type="text"
              placeholder="Search for a city"
              className="p-2 rounded-lg text-black border-2 border-white m-2"
              value={query}
              onChange={(e) => setQuery(e.target.value)}
            />
            <button
              type="submit"
              onClick={handleSearch}
              className="p-2 m-2 rounded-lg text-black border-2 border-white"
            >
              Search
            </button>
          </form>
        </div>
        <div className="w-80 h-96 border-2 border-white rounded-2xl flex items-center justify-center flex-col shadow-3xl ">
          <h1 className="text-3xl text-white font-bold">Weather Pro</h1>
          <br />
          <h2 className="text-2xl text-white font-semibold">
            {currentWeather?.location?.name}
          </h2>
          <br />
          <h3 className="text-2xl text-white font-medium">
            {currentWeather?.current?.temp_c}
          </h3>
          <br />
          <h4 className="text-2xl text-white">
            {currentWeather?.current?.condition?.text}
          </h4>
          <br />
          <img
            src={`https:${currentWeather?.current?.condition?.icon}`}
            alt="weather icon"
          />
          <button
            className="w-6 h-6 rounded-full bg-white text-blue-600 flex items-center justify-center hover:scale-110 transition"
            onClick={handlebookmark}
          >
            <Plus />
          </button>
        </div>
        <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-6 mt-8">

        {savedCities.map((city) => (
          
          <div
            key={city}
            className="w-72 h-40 bg-white text-blue-700 rounded-xl flex flex-col items-center justify-center shadow-lg p-4 space-y-2"
          >
            
            <h1 className="text-3xl text-white font-bold">{city}</h1>
            <button
              onClick={() => {
                getCurrentWeather(city)
                  .then(setCurrentWeather)
                  .catch(console.error);
              }}
              className="w-6 h-6 rounded-full bg-white text-blue-600 flex items-center justify-center hover:scale-110 transition"
            >
              ðŸ”„
            </button>
          </div>
        ))}
        </div>
      </div>
    </>
  );
};

export default Home;
